@font-face {
  font-family: "OpenSans-Regular";
  src: url("../fonts/Inter-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-SemiBold";
  src: url("../fonts/Inter-Regular.woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Bold";
  src: url("../fonts/Inter-Regular.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy-Regular";
  src: url("../fonts/Inter-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Inter-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans-Regular", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 155%;
  color: #54585D;
}

.container {
  margin: 0 auto;
  max-width: 1230px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.logo {
  line-height: 0;
}
.logo__img {
  width: 288px;
  height: 27px;
  fill: #2C3136;
}
@media screen and (max-width: 485px) {
  .logo__img--header {
    display: none;
  }
}
.logo__img--white {
  width: 260px;
  height: 25px;
  fill: #FFFFFF;
}
@media screen and (max-width: 860px) {
  .logo__img--white {
    width: 215px;
    height: 20px;
  }
}
@media screen and (max-width: 740px) {
  .logo__img--white {
    width: 260px;
    height: 25px;
    margin-bottom: 30px;
  }
}
.logo__img-already {
  display: none;
}
@media screen and (max-width: 485px) {
  .logo__img-already {
    width: 145px;
    height: 38px;
    display: inline-block;
  }
}

@media screen and (max-width: 1095px) {
  .nav--header {
    display: none;
  }
}
.nav--bottom {
  display: none;
}
@media screen and (max-width: 1095px) {
  .nav--bottom {
    display: inline-block;
  }
}

.menu {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 740px) {
  .menu--bottom {
    max-width: 300px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px 0;
  }
}
.menu__item {
  margin-right: 20px;
  line-height: 0;
  color: #2C3136;
  transition: color 0.4s;
}
.menu__item:hover {
  color: #3E89F4;
}
.menu__item:last-child {
  margin-right: 0;
}
@media screen and (max-width: 400px) {
  .menu__item--less {
    margin-right: 10px;
    font-size: 13px;
  }
}
.menu__item--white {
  margin-right: 25px;
  color: #FFFFFF;
}
.menu__item--white:hover {
  color: #FFFFFF;
}
@media screen and (max-width: 768px) {
  .menu__item--white {
    margin-right: 20px;
  }
}
@media screen and (max-width: 740px) {
  .menu__item--white {
    margin-right: 15px;
  }
  .menu__item--white:nth-child(2) {
    margin-right: 0;
  }
}
.menu__link {
  line-height: 22px;
}

.link-blue {
  padding: 20px 30px;
  background-color: #3E89F4;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 150%;
  border-radius: 4px;
  transition: background-color 0.4s;
}
.link-blue:hover {
  background-color: #5EA0FF;
}

.title {
  margin: 0;
  font-family: "OpenSans-SemiBold", sans-serif;
  font-weight: 600;
  font-size: 36px;
  line-height: 49px;
  color: #2C3136;
}

.text {
  margin: 0;
}

.input {
  all: unset;
  margin-bottom: 30px;
  padding: 15px 30px;
  width: 100%;
  background-color: #F5F5F5;
  border-radius: 10px;
  cursor: pointer;
}

.radio {
  all: unset;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: 2px solid #000000;
  border-radius: 50%;
}
.radio--man:checked + .registration-hero__text--man {
  color: #000000;
}
.radio--woman:checked + .registration-hero__text--woman {
  color: #000000;
}
.radio--married:checked + .registration-hero__text--married {
  color: #000000;
}
.radio--unmarried:checked + .registration-hero__text--unmarried {
  color: #000000;
}

.radio:checked {
  background-color: #3E89F4;
  border: 2px solid #B6D4FF;
}

