    max-width: 215px;
    flex-direction: column;
  }
}
.footer__copyright {
  font-size: 12px;
  line-height: 16px;
  color: #FFFFFF;
}
@media screen and (max-width: 425px) {
  .footer__copyright {
    margin-bottom: 15px;
  }
}
.footer__link {
  font-size: 12px;
  line-height: 16px;
  color: #FFFFFF;
}

.registration-hero {
  padding-top: 100px;
  padding-bottom: 150px;
}
.registration-hero__wrap {
  display: flex;
  flex-direction: column;
}
.registration-hero__title {
  margin-bottom: 40px;
  font-family: "OpenSans-Bold", sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 44px;
  color: #000000;
}
.registration-hero__form {
  display: flex;
  width: 100%;
}
.registration-hero__avatar {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
  margin-right: 70px;
  padding-bottom: 50px;
  width: 220px;
  height: 220px;
  border: 1px solid #D6D6D6;
  border-radius: 50%;
  cursor: pointer;
}
.registration-hero__file {
  display: none;
  width: 220px;
  height: 220px;
  border: 1px solid #D6D6D6;
  outline: none;
}
.registration-hero__user-photo {
  position: absolute;
  width: 71px;
  height: 71px;
  top: 53px;
  left: 50%;
  transform: translateX(-50%);
}
.registration-hero__text {
  line-height: 120%;
}
.registration-hero__text--grey {
  color: #757575;
}
.registration-hero__text--man {
  margin-right: 25px;
}
.registration-hero__text--married {
  margin-right: 25px;
}
.registration-hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 525px;
  width: 100%;
  padding-top: 20px;
}
.registration-hero__heading {
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 130%;
  color: inherit;
}
.registration-hero__heading--no-margin {
  margin-bottom: 0;
}
.registration-hero__heading--citizenship-arrow {
  position: relative;
}
.registration-hero__heading--citizenship-arrow::after, .registration-hero__heading--citizenship-arrow::before {
  position: absolute;
  content: "";
  width: 13px;
  border-top: 1px solid #54585D;
  top: 60px;
  left: 545px;
  z-index: 1;
  transform: rotate(45deg);
}
.registration-hero__heading--citizenship-arrow::before {
  top: 60px;
  left: 555px;
  transform: rotate(-45deg);
}
.registration-hero__heading--region-arrow {
  position: relative;
}
.registration-hero__heading--region-arrow::after, .registration-hero__heading--region-arrow::before {
  position: absolute;
  content: "";
  width: 13px;
  border-top: 1px solid #54585D;
  top: 60px;
  left: 545px;
  z-index: 1;
  transform: rotate(45deg);
}
.registration-hero__heading--region-arrow::before {
  top: 60px;
  left: 555px;
  transform: rotate(-45deg);
}
.registration-hero__floor {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 155px;
  width: 100%;
  margin-bottom: 30px;
}
.registration-hero__family {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 395px;
  width: 100%;
  margin-bottom: 30px;
}
.registration-hero__citizenship:checked .registration-hero__heading--citizenship-arrow::after {
  transform: rotate(45deg);
}
.registration-hero__citizenship:checked .registration-hero__heading--citizenship-arrow::before {
  transform: rotate(-45deg);
}
.registration-hero__region:checked .registration-hero__heading--region-arrow::after {
  transform: rotate(45deg);
}
.registration-hero__region:checked .registration-hero__heading--region-arrow::before {
  transform: rotate(-45deg);
}
.registration-hero__inner {
  display: flex;
  justify-content: space-between;
  max-width: 295px;
  width: 100%;
  margin-bottom: 10px;
}
.registration-hero__btn {
  color: #3E89F4;
  position: relative;
  transition: color 0.4s;
}
.registration-hero__btn::after {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 1px solid #3E89F4;
  top: 80%;
  left: 0;
  transition: border-top 0.4s;
}
.registration-hero__btn:hover {
  color: inherit;
}
.registration-hero__btn:hover::after {
  border-top: 1px solid inherit;
}

.spollers__item {
  margin-bottom: 10px;
  padding: 25px 0;
  padding-left: 70px;
  background-color: transparent;
  position: relative;
}
.spollers__item:last-child {
  margin-bottom: 0;
}
.spollers__item::after {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 1px solid #000000;
  opacity: 0.1;
  bottom: 0;
  left: 0;
}
.spollers__title {
  font-family: "Raleway-Medium", sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 130%;
  position: relative;
  width: 100%;
  text-align: left;
  color: #034FBC;
}
.spollers__text {
  padding-top: 5px;
  line-height: 130%;
  color: #034FBC;
}
.spollers.init .spollers__title::before, .spollers.init .spollers__title::after {
  content: "";
  position: absolute;
  left: -40px;
  top: 15px;
  width: 15px;
  border-top: 2px solid #034FBC;
  transition: transform 0.3s ease 0s;
}
.spollers.init .spollers__title::after {
  left: -49px;
  transform: rotate(45deg);
}
.spollers.init .spollers__title.active::after {
  transform: rotate(-45deg);
}
.spollers.init .spollers__title::before {
  transform: rotate(-45deg);
}
.spollers.init .spollers__title.active::before {
  transform: rotate(45deg);
}